.loading_box {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  width: 350px;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  font-size: 40px;
}
